@import url(https://bootswatch.com/4/cerulean/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.show {
  display: block;
}

.cardWeather {
  width: 250px;
  text-align: center;
  position: absolute;
  right: 5px;
  top: 5px;
}

.inputT {
  width: 30%;
  margin: auto;
}

.complete {
  text-decoration: line-through;
}

.error {
  background-color: pink;
  color: red;
  width: 200px;
  padding: 1rem;
  border-radius: 10px;
  margin: 1rem auto;
}

.container-tasks {
  display: flex;
  justify-content: space-evenly;
}

.container-ta {
  display: flex;
  align-items: center;
  justify-content: center;
}

